export interface ThreatActorProfile {
  id: string;
  name: string;
  aliases: string[];
  description: string;
  ttps: AttributedTTP[];
}

export interface TTPInputParam {
  name: string;       // Parameter name passed to script
  label: string;      // Display label in UI
  type: 'ip' | 'hostname' | 'url' | 'domain' | 'subnet' | 'text';
  placeholder: string;
  required: boolean;
}

export interface AttributedTTP {
  id: string; // MITRE ID
  technique: string;
  tactic: string; // e.g. "Discovery", "Credential Access"
  description: string;
  commandSnippet: string; // The raw command for display
  scriptPath?: string; // Path to our .ps1 implementation
  revertScriptPath?: string; // Path to revert script (if applicable)
  inputParams?: TTPInputParam[]; // Input parameters for tester
}

